*{
  margin: 0;
  padding: 0;
}
body{
  display: flex;
  font-family: sans-serif;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 10px;
  min-height: 100vh;
}
.form{
  box-shadow: 0px 0px 10px gray;
  padding: 20px;
  width: 315px;
  border-radius: 5px;
}
h1 {
  text-align: center;
  font-size: 18px;
}
.link{
  width: 300px;
  height: 30px;
  margin-top: 15px;
  text-align: center;
  border: 2px solid black;
  border-radius: 5px;
}
.Get{
  width: 314px;
  display: block;
  background-color: gray;
  border: none;
  padding: 5px;
  font-size: 18px;
  padding: 12px 25px;
  margin-top: 10px;
  border-radius: 5px;
  cursor: pointer;
  outline: none;
}
.hidden{
  display: none;
}
.hidden.active{
  display: block;
}